import { Component, OnInit } from '@angular/core';
import { ProductService } from '../../../../../auth/_services/product.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
// import { SideBarService } from '../../../../layouts/side-bar/side-bar.service';
// import { SideBarToggleComponent } from '../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { DataService } from '../../../../../_services/data.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
    selector: 'app-add-tag-list',
    templateUrl: "add-tag.component.html",
    styles: []
})
export class AddTagComponent implements OnInit {


    form: FormGroup;
    private formSumitAttempt: boolean;
    constructor(private formBuilder: FormBuilder, private _dataService: DataService, private _productService: ProductService) {


    }
    public userData;
    public searchValue = "";
    public dropDownValue = '';
    public noOfPage = '';
    public attributeData = '';
    public selectedStatus = '';
    public product_id: any;
    public user_id = '';

    public currentUser: any;
    p: number = 1;
    ngOnInit() {

        this.form = this.formBuilder.group({
            tag_name: [null, Validators.required],
            description: [null, Validators.required],
            status: [null, Validators.required]
        });

        if (JSON.parse(localStorage.getItem('product'))) {
            this.product_id = JSON.parse(localStorage.getItem('product'));
        }

        this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
        this.user_id = this.currentUser.success.id;
    }


    isFieldValid(field: string) {
        return (
            //console.log('this.form.get',this.form.get(field));
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
        );
    }

    displayFieldCss(field: string) {
        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };
    }

    onSubmit() {
        this.formSumitAttempt = true;
        if (this.form.valid) {
            let that = this;
            console.log(this.product_id);
            this._productService.addTag({ form_data: this.form.value, fk_user_id: this.user_id }).subscribe(
                data => {
                    //this.attributeData = data.success;
                    console.log(data);
                    this.attributeData = data.data;
                    // that.form.c;
                    this.reset();
                }
            );
            /*this._productService.addTag({ form_data: this.form.value, fk_user_id: this.user_id, fk_product_id: parseInt(this.product_id) }).subscribe(
                data => {
                    //this.attributeData = data.success;
                    console.log(data);
                    this.attributeData = data.data;
                    // that.form.c;
                    this.reset();
                }
            );*/
        }
    }

    reset() {
        if (this.selectedStatus != '') {
            this.form.reset();
            this.selectedStatus = '';
            this.formSumitAttempt = false;
        }
    }




    public search() {

        this.formSumitAttempt = true;

    }
    public vendor_delete(del_id) {


    }
    public changeStatus(user: any) {

    }

}
